.card {
  background: white;
  padding: 25px;
  border-radius: 8px;
  box-shadow: 0 0 10px #ccc;
  max-width: 600px;
  margin: 30px auto;
}

.card h2 {
  text-align: center;
  margin-bottom: 20px;
}

label {
  font-weight: bold;
  margin-top: 10px;
  display: block;
}

input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

button {
  margin-top: 15px;
  width: 100%;
  padding: 12px;
  background: #5a3d3d;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

button:hover {
  background: #462e2e;
}

.result {
  margin-top: 15px;
  font-weight: bold;
  text-align: center;
}
